	module.exports = {"data":{"challengeNode":{"title":"Disable DNS Prefetching with helmet.dnsPrefetchControl()","guideUrl":null,"description":["温馨提醒，本项目在 <a href='https://glitch.com/#!/import/github/freeCodeCamp/boilerplate-infosec/'>这个 Glitch 项目</a> 的基础上进行开发。你也可以从 <a href='https://github.com/freeCodeCamp/boilerplate-infosec/'>GitHub</a> 上克隆。","为了提高性能，大部分浏览器都提交获取页面上链接的 DNS 记录。这样的话当用户点击一个链接的时候浏览器已经提前知道 IP 地址了。但这也会造成 DNS 服务的过度使用（如果你是一个大型网站，有这千万级的用户），隐私问题 (窃听者可以推测你在某个页面上)，和页面数据准确性（有些没访问过的链接会被标记成已访问）。如果你对安全性要求比较高，你应该禁用 DNZ 预加载。当然，这样做性能上会有一点点损失."],"challengeType":2,"fields":{"blockName":"Information Security with HelmetJS","slug":"/information-security-and-quality-assurance/information-security-with-helmetjs/disable-dns-prefetching-with-helmet-dnsprefetchcontrol","tests":[{"text":"helmet.dnsPrefetchControl() 中间件应该被正确加载","testString":"getUserInput => $.get(getUserInput('url') + '/_api/app-info').then(data => { assert.include(data.appStack, 'dnsPrefetchControl'); assert.equal(data.headers['x-dns-prefetch-control'], 'off'); }, xhr => { throw new Error(xhr.responseText); })"}]}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/information-security-and-quality-assurance/information-security-with-helmetjs/disable-client-side-caching-with-helmet-nocache","id":"587d8248367417b2b2512c3d"},"slug":"/information-security-and-quality-assurance/information-security-with-helmetjs/disable-dns-prefetching-with-helmet-dnsprefetchcontrol"}}

